body {
    margin: 0;
    overflow: auto;
    overflow-x: hidden;
    height: 300vh;
    width: 100vw;
    background: #0b192f;
}

main {
    height: 100vh;
    width: 100vw;
    max-width: 99vw;
    margin: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    gap: 5vw;
    background: #0b192f;
    color: #ccd6f6;
    overflow: hidden;
}

.bg{
    color:#0b192f;
    display:inline;
}

button{
    height:6vh;
    background: rgb(239, 109, 35);
    font-size: 3vh;
}

button:hover{
    background: rgb(28, 183, 215);
    transition: 0.3s;
}

h1 {
    font-size: 5vw;
    margin: 0;
}

p {
    font-size: 2.5vw;
    margin:0;
}

h3{
    font-size: 1.5vw;
    margin:0;
    display: inline;
}

video {
    height:70vh;
    max-width:70vw;
    overflow:hidden;
    z-index: 0;
    border-radius:.5vw;
}

form {
    display: flex;
    flex-flow: column nowrap;
    width: 30vw;
    font-size: 1.5vw;
    background:white;
    border-radius: .5vw;
    padding:3vh;
    color:black;
}

input {
    height: 4vh;
    font-size: 2.6vh;
    padding:1vh;
}

label{
    margin-top:2vh;
}

textarea {
    height: 15vh;
    font-size:2.6vh;
    padding:1vh;
    margin-bottom: 2vh;
}


.carousel {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    gap:2vh;
}

.central {
    display: flex;
    flex-flow: column nowrap;
    justify-content: start;
    align-items: start;
    width: 45vw;
}

.link{
    text-decoration: underline;
}

.fade-out{
    opacity:0;
    transition:.5s ease-in;
}

.fade-in{
    opacity:100%;
    transition:.5s ease-in;
}

.link:hover{
    color:rgb(28, 183, 215);
    cursor: pointer;
    transition:.3s;
    font-size: 3vw;
}

.hover-letter-orange{
    display:inline;
    transition:2s ease-out;
}

.hover-letter-orange:hover{
    color:rgb(239, 109, 35);
    text-shadow:-.4vw -.4vw rgb(28, 183, 215);
    font-size:7vw;
    transition: .1s;
}

.hover-letter-container{
    white-space: nowrap;
}

.icon {
    z-index: 1;
    color: blue;
    user-select: none;
}

.icon:hover{
    color:green;
    transform: scale(1.3)
}

.blue {
    color: rgb(28, 183, 215);
    text-shadow: .2vw .2vw rgb(239, 109, 35);
    /*     color: rgb(239,109,35);
    text-shadow: 1px 1px rgb(28,183,215); */
}

.orange {
    color: rgb(239, 109, 35);
    /*     color: rgb(28,183,215);
    text-shadow: 1px 1px rgb(239,109,35); */
}

.side {
    width: 30vw;
    height: 100vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

.video-viewer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
}

.caption {
    z-index: 1;
    border-radius: 1vh;
    width: 100%;
}


.video-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    height:70vh;
    width:70vw;
}

.flex-row {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    align-items: start;
    gap: 5vw;
}

#root {
    margin: 0;
}